.team {
  display: flex;
  justify-content: center;
  color: #545454;

  &_header {
    color: #ff6b08;
    font-size: 2.5rem;
    letter-spacing: 0.04em;
    padding-bottom: 1.5em;
  }
  &_list {
    gap: 30px;
    padding: 0 30px;
    flex-wrap: wrap;
  }
  &_names {
    padding-block: 15px;
  }
}

.modal-btn--container {
  align-self: flex-end;
}

.modal-btn {
  border: none;
  background-color: transparent;
  padding-top: 15px;
  padding-right: 15px;
}

.modal-btn-icon {
  transition: $transitionDefault;

  &:hover {
    transform: rotate(180deg);
  }
}

.picture {
  width: 200px;
  // height: 230px;
}

.box__image {
  box-sizing: border-box;
  justify-content: center;
  text-align: center;
  margin: 0;
  gap: 30px;
  background-color: var(--active-text-color);
  justify-content: center;
  text-align: center;
  margin: 0.4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
  border: 1px solid #eeeeee;
  transition-property: box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  margin: 0;
  cursor: pointer;
}
.overlay {
  position: relative;
  overflow: hidden;
}
.img-content {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.56;
  padding-left: 24px;
  padding-right: 45px;
  text-align: left;
  padding-block: 49px;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #ff6b08;
  top: 100%;
  transition: top 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.box__image:hover .img-content {
  top: 0;
  cursor: pointer;
}

.backdrop {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  transition: $transitionDefault;
  display: flex;
  z-index: 2;
}
.is-hidden {
  display: none;
}
.modal-content {
  @include flex();
  justify-content: flex-start;
  flex-direction: column;
  gap: 2em;
  margin: auto;
  height: 90%;
  width: 80%;
  max-width: $breakpointDesktop;
  background-color: #f7f7f7;
  border-radius: 3px;
  max-height: calc(100vh - 110px);
  overflow-y: auto;
  transition: $transitionDefault;
}
